
.pure-g {
    height: auto !important;
}
.pure-u-1-2 {
    height: auto;
        min-height: 48vh !important;
    position: relative;
    overflow: hidden;
}
#tv {
    background-color: black;
}
#movie {
    background-color: black;
}
.circle {
    z-index: 2;
    width: 120px;
    height: 120px;
    position: absolute;
}
.circle > a {
    text-decoration: none;
    color: white;
}
.pureimg {
    height: 100% !important;
    width: 100%
}
.post-info {
    background-color: rgba(0,0,0,0.8);
    position: absolute;
    color: white;
    margin-top: -50px;
    z-index: 3;
}
.post-info-left {
    bottom: -10px;
    left: 10px;
}
.post-info-right {
    bottom: -10px;
    right: 10px;
}
.carousel-button {
    font-size: 13px !important;
    background-color: #C13939 !important;
    position: absolute;
    z-index: 3;
}
.carousel-button-left {
    bottom: 40px;
    left: 10px;
}
.carousel-button-right {
    bottom: 40px;
    right: 10px;
}
.carousel-button > a {
    color: white;
    text-decoration: none !important;
}
#red-circle{
    bottom: -60px;
    right: -60px;
    border-radius: 50%;
    background-color: #01062E;
    border: solid 2px black;
}
#red-circle > a {
    position: absolute;
    right: 70px;
    bottom: 70px;
}
#blue-circle{
    bottom: -60px;
    left: -60px;
    border-radius: 50%;
    background-color: white;
    border: solid 2px black;
}
#blue-circle > a {
    position: absolute;
    left: 70px;
    bottom: 70px;
    color: black;
}
#yellow-circle{
    top: -60px;
    right: -60px;
    border-radius: 50%;
    background-color: white;
    border: solid 2px black;
}
#yellow-circle > a {
    position: absolute;
    right: 70px;
    top: 70px;
    color: black;
}
#green-circle{
    top: -60px;
    left: -60px;
    border-radius: 50%;
    background-color: #01062E;
    border: solid 2px black;
}
#green-circle > a {
    position: absolute;
    left: 70px;
    top: 70px;
}
#news {
    border-right: 0px dashed;
    height: 48vh;
}
#news-area {
    margin: 60px 40px 40px 40px;
}
#trailer {
    height: 48vh;
}
.news-title{
    list-style: none;
    color: dimgray;
}
.news-title > a{
    color: black;
    text-decoration: none;
}
.hr0{ 
    height:1px;
    border:none;
    border-top:1.5px dashed gray;
}
@media (max-width: 1024px) {
.pure-g {
        height: auto !important;
}
.pure-u-1-2 {
        display: block;
        width: 100%;
        height: auto;
}
.carousel-button {
        bottom: 40px;
        left: 10px;
}
.post-info {
        bottom: -10px;
        left: 10px;
}
.circle {
        display: none;
}
}
@media (min-width: 1024px) {
.fade-enter-active, .fade-leave-active {
        transition: opacity .8s
}
.fade-enter, .fade-leave-active {
        opacity: 0
}
}



