
#news-area {
    width: 90%;
    height: 120vh; 
    margin: 20px auto 0;
}
.news-item {
    margin-bottom: 30px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    border-bottom: solid 2px rgba(128,128,128,0.2);
}
#gallery > img{
    margin-bottom: -5px;
    height: 120px;
    width: 120px;
}
#text-area {
    width: 80%;
    margin-left: 20px;
}
#text-area > a {
    text-decoration: none;
}
.hr0{ 
    height:1px;
    border:none;
    border-top:1.5px dashed gray;
}
#info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
#time {
    color: gray;
}
#trans {
    color: gray;
}



